<template>
	<button type="button" class="pay-item">付款 {{countText}}</button>
</template>


<script>

	export default {
		props: ['timer'],
		data(){
			return {
				countText: '',
				counterTimer: null,
				count: 0,
			}
		},
		computed:{
			
		},
		mounted(){
			let self = this;
			self.count = self.timer;
			self.counterTimer = setInterval(function(){
				let timers = self.count;
				let min = parseInt(timers/(1000*60));
				let sec = parseInt((timers - min*1000*60)/1000);
				sec = sec < 10 ? '0'+sec : sec;
				self.countText = min + ':' +sec;
				
				if(self.count < 0){
					clearInterval(self.counterTimer);
					return false;
				}else {
					self.count -= 1000;
				}
				
			},1000);
			
		}

	}



</script>


<style lang="scss">
	
	$baseFontSize: 750px;
	@function pxToRem($px) {
		@return $px / $baseFontSize * 10rem;
	}
	$defaultSize: pxToRem(24px);



	
</style>


